.avatarBox {
  position: relative;
  height: 300px;
  min-width: 300px;
  margin-right: 20px;
  border-radius: 30px;
  overflow: hidden;
  background: rgba(206, 177, 15, 0.541);
}

.avatarBox img {
  width: 100%;
  height: auto;
}

.addPhotoBtn {
  position: absolute;
  top: 10px;
  left: 20px;
}

.addPhotoBtn input{
  display: none;
}

.addPhotoBtn label{
  padding: 3px 5px;
  border: 1px solid black;
  border-radius: 5px;
  background: rgba(0, 0, 255, 0.8);
  font-size: 14px;
  color: white;
  cursor: pointer;
}

.addPhotoBtn label:hover, .addPhotoBtn label:active {
  border: 1px solid rgba(0, 0, 255, 0.8);
  background: white;
  color: rgba(0, 0, 255, 0.8);
  cursor: pointer;
}

.addPhotoBtn label:active {
  background: rgba(0, 0, 255, 0.3);
}
