.userInfo {
  position: relative;
  width: 100%;
  min-height: 100%;
  padding: 10px;
  border-radius: 30px;
  background: rgba(206, 177, 15, 0.541);
}

.userInfo * {
  margin: 0;
}

.fullName {
  text-align: center;
}

.addInfo {
  display: flex;
  justify-content: space-between;
}

.addInfo div {
  width: 50%;
}

.contactTitle, .descrJob, .aboutTitle {
  font-weight: bold;
}

.editBtn {
  position: absolute;
  right: 30px;
  padding: 3px 5px;
  border: 1px solid black;
  border-radius: 5px;
  background: rgba(0, 0, 255, 0.8);
  font-size: 14px;
  color: white;
}

.editBtn:hover, .btnSubmit:active {
  border: 1px solid rgba(0, 0, 255, 0.8);
  background: white;
  color: rgba(0, 0, 255, 0.8);
  cursor: pointer;
}

.editBtn:active {
  background: rgba(0, 0, 255, 0.3);
}


