.editFormBox {
  position: absolute;
  inset: 0;
  padding: 10px;
  border-radius: 30px;
  background: #fff;
}

.editForm {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

label {
  font-size: 16px;
  color: rgba(0, 0, 255, 0.8);
}

.inputBox {
  position: relative;
}

.errorMessage {
  position: absolute;
  bottom: -12px;
  font-size: 10px;
  color: red;
  z-index: 5;
}

.contacts .errorMessage {
  left: 140px;
}

.input {
  border: 1px solid rgba(0, 0, 255, 0.8);
  border-radius: 5px;
  font-size: 14px;
  color: rgba(0, 0, 255, 0.8);
}

.input.error {
  border: 1px solid rgba(255, 0, 0, 0.8);
  color: rgba(255, 0, 0, 0.8);
}

.contactsContainer {
  width: 50%;
}

.contactsContainer .inputBox {
display: flex;
justify-content: space-between;
margin-bottom: 10px;
}

.otherInfoContainer {
  padding: 0 15px;
  width: 40%;
}

.otherInfoContainer .inputBox {
  margin-bottom: 10px;
}

.otherInfoContainer .inputBox label {
  display: block;
  font-weight: bold;
}

.checkboxContainer {
  display: flex;
}

.checkboxContainer label {
  margin-right: 20px;
}

.otherInfoContainer .inputBox textarea {
  width: 100%;
  height: 60px;
  resize: none;
}

.btnSubmit {
  padding: 3px 5px;
  border: 1px solid black;
  border-radius: 5px;
  background: rgba(0, 0, 255, 0.8);
  font-size: 14px;
  color: white;
}

.btnSubmit:hover, .btnSubmit:active {
  border: 1px solid rgba(0, 0, 255, 0.8);
  background: white;
  color: rgba(0, 0, 255, 0.8);
  cursor: pointer;
}

.btnSubmit:active {
  background: rgba(0, 0, 255, 0.3);
}

.btnSubmit:disabled {
  border: 1px solid rgb(163, 163, 163);
  background: white;
  color: rgb(163, 163, 163);
  cursor: unset;
}

.errorForm {
  position: absolute;
  color: red;
  left: 50px;
  bottom: 5px;
}


