.profileInfo {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 500px;
  overflow: hidden;
}

.wallpaper {
  position: absolute;
  inset: 0;
  z-index: 1;
}

.wallpaper img {
  width: 100%;
  height: 100%;
}

.descriptionBox {
  padding: 10px;
  width: 100%;
  min-height: 300px;
  height: max-content;
  z-index: 10;
}

.description {
  display: flex;
  min-height: 100%;
  width: 100%;
}




